import React from 'react';
import { PopoutWrapper, Title, Text, Card, CardGrid, Cell, Counter, List, Button, Div } from '@vkontakte/vkui';

import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';

const url = "http://api.waste.hhos.ru.s64.hhos.ru/achievements/";

const PopoutAchievement =  ({achievement, onClose, name}) => (
    <PopoutWrapper onClick={onClose} >
      <CardGrid className={name}>
          <Card mode='shadow' style={{backgroundColor:"white"}} size="l">
              <div align="center">
                <Cell asideContent={<Icon24Dismiss onClick={onClose} />} before={<Counter mode="primary">!</Counter>}>
                    <Title mode="semibold" level={1}>Достижение!</Title>
                </Cell>
                    <Text mode="regular" style={{paddingBottom:15, padding:10}}>{"Вы получили достижение "+achievement.name}</Text>
                    <img style={{height:100, weight:100}} src={url+achievement.img}/>
                    <Text style={{padding:15}}>{achievement.description}</Text>
                    <Title mode="heavy" level={2}>
                      Вы получаете:
                    </Title>
                    <List>
                        <p style={{paddingTop:5, paddingBottom:5, margin:0}}>{"+ "+achievement.reward_kg+" кг"}</p>
                        <p style={{paddingTop:5, paddingBottom:5, margin:0}}>{"+ "+achievement.reward_k+"/c"}</p>
                    </List>
                    <Div>
                    <Button size='l' mode='outline'>Поделиться в истории</Button>
                    </Div>
                  
              </div>
          </Card>
      </CardGrid>
    </PopoutWrapper>
)

export default PopoutAchievement;

