import React from 'react';
import { Headline, Title, Cell, Div, Card, Avatar, PopoutWrapper, Counter, Button } from '@vkontakte/vkui';

import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';

const url = "http://api.waste.hhos.ru.s64.hhos.ru/";


const PopoutError = ({src, name, description,kg, onClose, share}) => (
    <PopoutWrapper onClick={onClose}>
    <Card style={{border:"1px solid  #F66565", backgroundColor:'#C66363'}}>
      <Div align="center">
          <Cell before={<Counter mode='prominent'>!</Counter>} asideContent={<Icon24Dismiss fill="#37200A" onClick={onClose}/>}>
            <Title level={3} weight="heavy">Ошибочка</Title>
          </Cell>
          <Avatar src={url+src} mode='image' size={56} style={{marginTop:10}}/>
          <h1>{name}</h1>
          <span style={{padding:6, display:'block'}}>
             {description}  
          </span>
          <Button style={{margin:10}} onClick={share}>Поделиться с друзьями</Button>
           <Headline>
             {"-"+kg+" кг"}
           </Headline>
      </Div>
    </Card>

</PopoutWrapper>
);

export default PopoutError;